@import url("https://fonts.googleapis.com/css2?family=Mulish&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
}
body {
  /* background-color: #b4f2e1; */
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
/*header part css start here*/

.tophead {
  width: 100%;
  height: 65px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  /* background: #1976d2; */
}

.brandlogo h1 {
  color: #fff;
  font-size: 1.5rem;
  padding: 1rem;
  color: #333333;
  font-family: "Roboto", sans-serif;
}
.extra-headings {
  margin-left: 40rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.extra-headings h2 {
  font-size: 1rem;
  padding: 1rem;
  color: #333333;
  font-family: "Roboto", sans-serif;
}
.menulines {
  font-size: 30px;
  cursor: pointer !important;
  margin-left: 1px;
}
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background: #272634;
  overflow-x: hidden;
  padding-top: 60px;
  transition: all 0.5s linear;
}
.sidenav a {
  text-decoration: none;
  font-size: 20px;
  color: #fff !important;
  display: block;
  padding: 25px 0 25px 20px;
  text-transform: capitalize;
}
.sidenav a:hover {
  color: #f9b707;
}
.closebtn {
  position: absolute !important;
  top: 0;
  right: 25px;
  font-size: 40px !important;
  cursor: pointer !important;
}
@media screen and (max-width: 800px) {
  .brandlogo h1 {
    font-size: 0.8rem;
    padding-right: 1px;
  }
  .extra-headings {
    margin-left: 1rem;
  }
  .extra-headings h2 {
    color: #333333;
    font-size: 0.7rem;
    padding: 0.5rem;
  }
}
/* header end here*/
.navbar {
  background-color: #fff !important;
  margin-top: auto;
}
.navbar-brand {
  font-size: 1.9rem !important;
  color: #565387 !important;
}
.navbar a {
  font-size: 1.2rem !important;
  text-transform: capitalize;
}
.menu_active {
  font-size: bold;
  border-bottom: 2px solid #565387;
}
.navbar a:hover {
  color: #3498db !important;
}
@media (max-width: 991px) {
  .navbar-brand {
    font-size: 1.5rem !important;
  }
  .menu_active {
    font-size: bold;
    border-bottom: none;
  }
}
/* main landing section */
/* =========================================================================================== */
#header {
  width: 100%;
  height: 80vh;
  margin-top: 10px;
}
#header h1 {
  margin: 0 0 10px 0;
  font-size: 2rem;
  font-weight: 700;
  color: black;
}
#header .brand-name {
  color: #3498db;
}
#header h2 {
  color: #484848;
  font-size: 1.7rem;
}
#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  color: #3498db;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover {
  background: #3498db;
  color: #fff;
}
