/* ================================================ */
/* search page here */
.searchbar {
  width: 50%;
  height: auto;
  margin: 0 auto;
  text-align: center;
}
.searchbar input {
  margin: 30px 0;
  padding: 5px 10px;
  border-radius: 5px;
  font-family: "Josefin Sans", sans-serif;
  outline: none;
}
.myimg img {
  width: 30rem;
  height: 23rem;
}
@media (max-width: 800px) {
  .searchbar input {
    margin: 40px 0;
    padding: 5px 5px;
  }
  .myimg img {
    width: 15rem;
    height: 10rem;
  }
}
.img-netfilx {
  height: 14rem;
  width: 100%;
}

/* =========================================================================todolist css hrer */

.main_div {
  width: 100%;
  height: 100vh;
  background: #7bed9f;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div {
  width: 25%;
  height: 70vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}

.todoheading{
  color: rgba(0, 0, 0, 0.87);
  font-size: 50px;
  margin-bottom: 30px;
  min-width: 64px;
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  border-radius: 4px;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
}

.center_div input {
  text-align: center;
  height: 30px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  margin-right: 20px;
}

.newBtn {
  width: 50px;
  height: 55px;
  background-color: #16a085 !important;
  color: white !important;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  border-radius: 50% !important;
  outline:none !important;
}

.newBtn:hover {
  color: #16a085 !important;
  background: white !important;
}

/* //the add symbol inside the button in app comp */

.MuiSvgIcon-root {
  width: 2rem !important;

  height: 2rem !important;
}

ol {
  margin-top: 30px;

  /* margin-right: 50px; */
}

ol li {
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  list-style: none;

}
.todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.todo_style .deleteIcon {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 5px 35px;
  padding: 5px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.todo_style .deleteIcon:hover {
  background-color: transparent;
  color: crimson;
  box-shadow: 5px 5px 15px -5px rgba(255, 0, 0, 0.5);
}
.todo_style .fa-times:hover .list_style {
  color: crimson;
}
@media (max-width: 768px) {
  .center_div {
    width: 80%;
  }
  .todo_style .fa-times {
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  /* button {
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;
  } */
  .todoheading{
    font-size: 40px;
   
  }
}
